	module.exports = {"data":{"markdownRemark":{"html":"<p>今月はさすが神無月だけあって災害だらけだ。 仕事中、業者さんが杭基礎打ち込んでいたら地下の高圧線破っちゃって隣のビル夜まで停電させてしまうし。おかげで390円(税込)の中華そば食い損ねた。もしもう5cm敷地内から見て外側に打ち込んでたら丸井、ダイエーをはじめ、ソニックシティからJACKまで大宮の電力が全てストップしてたと電気屋が言うから大騒ぎ。まぁ、停電が隣のビルだけで済んだのも感電者が出なかったのも不幸中の幸いか。 災害といえば 地震多いなぁ。そろそろ関東大震災の時期かな。 さてここで予想。次の大地震が起きるのは、<em>1923/9/1</em>だ。間違いない！</p>","frontmatter":{"cover":null,"date":"2004-10-17T00:00:00+09:00","strDate":"2004/10/16","title":"神無月"}}},"pathContext":{"path":"/ja/blog/2004/10/17-神無月/","langKey":"ja"}}

